import { formateDate } from '../utils/validate'
import { mapState } from 'vuex'
import api from '../api'
export default {
  name: 'column',
  data () {
    return {
      id: this.$route.params.id
    }
  },
  created () {
    this.$store.dispatch({type: 'getDetail', url: api.getDetail(this.id)})
  },
  computed: {
    ...mapState([
      'msg',
      'detail'
    ])
  },
  filters: {
    parseTime (val) {
      return formateDate(val)
    }
  }
}



